/* start of the style of the navbar in that app */
.navbar .navbar-brand
{
  position: relative;
  font-size: 33px;
  cursor: pointer;
  color: #fff;
  letter-spacing: 2px;
}
.navbar .navbar-list-container ul li
{
  padding: 20px;
  position: relative;
  margin: 4px;
}
.navbar .navbar-list-container ul li a:hover::before
{
  opacity: 1;
  width: 70%;
  visibility:visible;
} 
.navbar .navbar-list-container ul li a:hover 
{
  color: rgb(250, 158, 72);
}
.navbar .navbar-list-container ul li a::before
{
  position: absolute;
  content: " ";
  bottom: -20%;
  width: 0px;
  height: 2px;
  background-color: rgb(235, 141, 54);
  transition: .5s ease-in-out;
  opacity: 0;
  visibility: hidden;
}
.navbar .navbar-list-container ul li a.active::before
{
  width: 70%;
  visibility: visible;
  opacity: 1;
} 
.navbar .navbar-list-container ul li a.active 
{
  color: rgb(235, 141, 54);
}
.navbar .navbar-list-container ul li a
{
  font-size: 16px;
  font-weight: 500;
  text-decoration:none;
  color: rgb(209, 209, 209);
  transition:all .3s ease-in-out;
} 
.navbar .navbar-list-container ul 
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction:row;
}
.navbar .started-div .fa 
{
  display: none;
}
.navbar .started-div .get-started-btn:hover
{
  background-color: rgb(235, 141, 54);
} 
.navbar .started-div .get-started-btn 
{
  position: relative;
  color: #fff;
  background-color:  rgb(250, 158, 72);
  text-decoration: none;
  padding: 8px 20px;
  border-radius: 50px;
  transition: .3s;
}
.navbar .started-div 
{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}
#hero h3,
#hero h1
{
  color: #fff;
  text-align: center;
}
#hero .play-btn {
  width: 94px;
  height: 94px;
  margin: 0 auto;
  background: radial-gradient(rgb(235, 141, 54) 50%, rgba(255, 168, 86, 0.323) 52%);
  border-radius: 50%;
  display: block;
  overflow: hidden;
  position: relative;
}

#hero .play-btn::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-40%) translateY(-50%);
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 15px solid #fff;
  z-index: 100;
  transition: all 400ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

#hero .play-btn::before {
  content: '';
  position: absolute;
  width: 120px;
  height: 120px;
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
  -webkit-animation: pulsate-btn 3s;
  animation: pulsate-btn 3s;
  -webkit-animation-direction: forwards;
  animation-direction: forwards;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-timing-function: steps;
  animation-timing-function: steps;
  opacity: 1;
  border-radius: 50%;
  border: 5px solid rgb(235, 141, 54);
  top: -15%;
  left: -15%;
  background: rgba(198, 16, 0, 0);
}

#hero .play-btn:hover::after {
  border-left: 15px solid rgb(235, 141, 54);
  transform: scale(20);
}
@media screen and (max-width:470px){
  #hero h3 
  {
    font-size: 20px;
    margin-top: 10px;
  }
  #hero h1
  {
    font-size: 26px;
  }
}
#hero .play-btn:hover::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-40%) translateY(-50%);
  width: 0;
  height: 0;
  border: none;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 15px solid #fff;
  z-index: 200;
  -webkit-animation: none;
  animation: none;
  border-radius: 0;
}
#hero 
{
  margin-top: 200px;
}
@-webkit-keyframes pulsate-btn {
    0% {
      transform: scale(0.6, 0.6);
      opacity: 1;
    }
    100% {
      transform: scale(1, 1);
      opacity: 0;
    }
  }
  
  @keyframes pulsate-btn {
    0% {
      transform: scale(0.6, 0.6);
      opacity: 1;
    }
    100% {
      transform: scale(1, 1);
      opacity: 0;
    }
  }
