  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.816);
  z-index: 999;
  transition: .5s ease-in;
}
.glightbox-container
{
  opacity: 0;
  transition: .5s ease-in;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
